import React from 'react';

export function Services() {
    const servicesObject = [
        {
            key: 1,
            imgSrc: "https://img.icons8.com/?size=100&id=12447&format=png&color=ffffff",
            heading: 'Software Development',
            des: "Custom websites, portals, marketplaces and platforms.",
        },
        {
            key: 2,
            imgSrc: "https://img.icons8.com/?size=100&id=24870&format=png&color=ffffff",
            heading: 'Architecture & Design',
            des: "designing and architecting robust and scalable solutions.",
        },
        {
            key: 7,
            imgSrc: "https://img.icons8.com/?size=100&id=4pA6aw4OTuwp&format=png&color=ffffff",
            heading: 'AI & Machine Learning',
            des: "Automate tasks, improve accuracy, and unlock new business opportunities.",
        },
        {
            key: 4,
            imgSrc: "https://img.icons8.com/?size=100&id=2zyvaojJtgOA&format=png&color=ffffff",
            heading: 'DevOps & Cloud Services',
            des: "Accelerate development cycles, improve application performance, and reduce operational costs.",
        },
        {
            key: 5,
            imgSrc: "https://img.icons8.com/?size=100&id=11678&format=png&color=ffffff",
            heading: 'Security Services',
            des: "Vulnerability assessments and penetration testing to identify and mitigate potential threats.",
        },
        {
            key: 6,
            imgSrc: "https://img.icons8.com/?size=100&id=ZQ9axxszRfad&format=png&color=ffffff",
            heading: 'Data Science & Analytics',
            des: "Transform data into actionable insights for improved business performance and strategic growth.",
        },

        {
            key: 8,
            imgSrc: "https://img.icons8.com/?size=100&id=52230&format=png&color=ffffff",
            heading: 'Product Strategy & Consulting',
            des: "From Concept to Launch: Product Strategy That Delivers.",
        },
        {
            key: 3,
            imgSrc: "https://img.icons8.com/?size=100&id=22136&format=png&color=ffffff",
            heading: 'Quality Assurance & Testing',
            des: "Testing services to ensure your software meets the highest standards of quality, reliability, and user satisfaction.",
        },
    ];

    return (
        <div>
            {/* Main Heading for Services */}
            <p className="text-3xl text-center font-bold py-16 lg:py-20  md:text-4xl lg:text-5xl">Services</p>
            <div className="grid sm:grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 lg:gap-16 text-white m-2 px-6 lg:px-20 pointer">
                {servicesObject?.map((service) => (
                    <div key={service.key} className="service-card border-2 px-2 border-[#5ecaf4] hover:border-2 rounded-xl h-auto py-8 mt-8 transition-all duration-300">
                        <div className="h-28 flex justify-center items-center">
                            <img src={service.imgSrc} alt={service.heading} className="w-24 h-24" />
                        </div>
                        <h1 className="text-center font-bold text-xl mt-4">{service.heading}</h1>
                        <p className="text-center p-4 mx-4 text-sm">{service.des}</p>
                    </div>
                ))}
            </div>

            {/* Add the CSS for hover effect */}
            <style jsx>{`
                .service-card {
                    transition: all 0.6s ease-in-out;
                    transition: all 0.3s ease-in;
                }
                .service-card:hover {
                    transform: scale(1.05); /* Slightly increase the size */
                }
            `}</style>
        </div>
    );
}
