import React, { useEffect } from "react";
import "particles.js";
import { TypeAnimation } from 'react-type-animation';
import { SocialIcons } from './SocialIcons';
import wasif_img from "./../assets/wasif0.png"
import { Navbar } from "./Navbar";

export function About() {

  useEffect(() => {
    // Initialize particles.js
    window.particlesJS("particles-js", {
      particles: {
        number: {
          value: 380,
          density: {
            enable: true,
            value_area: 2000,
          },
        },
        color: {
          value: "#5ecaf4",
        },
        shape: {
          type: "circle",
          stroke: {
            width: 2,
            color: "#5ecaf4",
          },
          polygon: {
            nb_sides: 2,
          },
        },
        opacity: {
          value: 0.9,
          anim: {
            enable: false,
          },
        },
        size: {
          value: 3,
          random: true,
          anim: {
            enable: false,
          },
        },
        line_linked: {
          enable: true,
          distance: 100,
          color: "#5ecaf4",
          opacity: 0.9,
          width: 1,
        },
        move: {
          enable: true,
          speed: 6,
          out_mode: "out",
        },
      },
      interactivity: {
        detect_on: "canvas",
        events: {
          onhover: { enable: true, mode: "grab" },
          onclick: { enable: true, mode: "push" },
        },
        modes: {
          grab: { distance: 140, line_linked: { opacity: 1 } },
          push: { particles_nb: 4 },
        },
      },
      retina_detect: true,
    });

    return () => {
      const particlesCanvas = document.getElementById("particles-js");
      if (particlesCanvas) particlesCanvas.innerHTML = "";
    };
  }, []);
  return (
    <>
      <div className="text-white relative lg:px-0">
        {/* add the partcles js animations */}
        <div id='particles-js' style={{ position: "absolute", width: "100%", height: "100%", zIndex: -1 }}></div>

        {/* add the Navbar  */}
        <Navbar />

        {/* start about section  */}
        <div className="mt-6 lg:mt-0">
          <p className="text-xl text-center font-bold lg:py-8 md:text-4xl lg:text-5xl">
            <TypeAnimation
              sequence={[
                'From Idea to Impact, Guiding Clients Every Step of the Way.',
              ]}
              wrapper="span"
              speed={20}
              deletionSpeed={0}
              omitDeletionAnimation={true}
              repeat={0}
            />
          </p>
        </div>
        <div className="flex flex-col md:flex-row justify-center items-center px-4 py-10">
          {/* Image Section with 30% width */}
          <div className="flex justify-center items-center mb-10 md:mb-0 md:w-4/12">
            <img
              className="rounded-full w-40 md:w-56 lg:w-96 border-2 border-[#5ecaf4]"
              src={wasif_img}
              alt="Wasif Shehzad"
            />
          </div>

          {/* Details Section with 70% width */}
          <div className="text-center md:text-left md:w-9/12 md:px-6">
            <h1 className="font-arial text-xl md:text-2xl lg:text-3xl font-extrabold text-white">
              Wasif Shehzad
            </h1>
            <h2 className="font-arial text-sm md:text-lg lg:text-xl font-semibold">
              Scaling and Accelerating Technology Startups
            </h2>

            <div className="text-sm text-justify px-2 lg:px-0 md:text-base lg:text-lg text-white mt-4 lg:pr-24">
              <p>
                I have been working as a Full Stack Engineer for the past 8+ years. I have worked in diverse domains like
                health, Fintech, Education, Security, Logistics, and Food. For the past few years, I have worked with
                multiple enterprise clients and multinational banks. While working there, I have developed many skills like
                leadership, best practices, business understanding, communication, and expertise in leading technologies.
              </p>
              <p className="py-4">
                As part of my technical expertise, I help my clients leverage the full potential of technology, and that's
                what I'm passionate about. My real strength is my ability to truly understand expectations and deliver
                impactful results. I take pride in adding value and making a meaningful impact.
              </p>
            </div>
            <SocialIcons />
          </div>
        </div>
      </div>
    </>
  );
}
