export const ProjectCard = (props) => {

    const { name, description, imageUrl, button } = props; // object destructer 

    return (

        <div className='pb-8 mb-8 lg:ml-10 lg:mr-10 '>
            <div className="w-80 xl:w-full h-full xl:pt-2  border border-[#5ecaf4] bg-blue rounded-2xl text-white">
                <div className="flex justify-center rounded-xl">
                    <img className="xl:w-11/12  shadow-blue shadow-2xl p-2 rounded-xl" src={imageUrl} alt="" />
                </div>
                <div className=" text-left text-white p-4">
                    <h1 className="font-extrabold text-2xl py-2 ">{name}</h1>
                    <p className="text-sm pt-2">{description}</p>
                    <div className='pt-5  '>
                        <button className='button-popup border border-[#5ecaf4] p-2 text-center text-sm font-medium text-white active:bg-gray-500  rounded-lg'> {button} </button>
                    </div>
                </div>
            </div>
            <style jsx>{`
                .button-popup {
                    transition: all 0.6s ease-in-out;
                    transition: all 0.3s ease-in;
                }
                .button-popup:hover {
                    transform: scale(1.05); /* Slightly increase the size */
                }
            `}</style>
        </div >

    )

}