import React from 'react'
import bgImg from "../assets/backgroundImg.png"

export function ContactFrom() {
    return (
        <>
            <p className="text-3xl text-center font-bold py-10 lg:py-10 md:text-4xl lg:text-5xl">Get In Touch</p>
            <div className="pb-20 px-2">

                <div className="flex flex-col sm:flex-row gap-8 items-center lg:px-24">
                    <div className="flex justify-center sm:w-1/2">
                        <img
                            className="tilte w-3/5 sm:w-full lg:w-4/5 rotate-img"
                            src={bgImg}
                            alt="Rotating Image"
                        />
                    </div>
                    <form id="form" className="flex px-4 lg:px-0 flex-col w-full sm:w-1/2">
                        <div className="inputBox flex flex-col sm:flex-row gap-4">
                            <input
                                type="text"
                                placeholder="Name"
                                className="box p-3 border border-[#5ecaf4] rounded-lg w-full bg-transparent focus:outline-none"
                            />
                            <input
                                type="email"
                                placeholder="Your Email"
                                className="box p-3 border border-[#5ecaf4] rounded-lg w-full bg-transparent focus:outline-none"
                            />
                        </div>
                        <input
                            id="subject"
                            type="text"
                            placeholder="Subject"
                            className="box p-3 border border-[#5ecaf4] rounded-lg my-4 w-full bg-transparent focus:outline-none"
                        />
                        <textarea
                            placeholder="Your Message"
                            name=""
                            id="message"
                            cols="30"
                            rows="10"
                            className="p-3 border border-[#5ecaf4] rounded-lg mb-4 w-full bg-transparent focus:outline-none"
                        ></textarea>

                        <div>
                            <button type='button' className="contact-btn p-2 pointer text-center text-sm font-medium text-white rounded-lg border border-[#5ecaf4]">
                                SEND
                            </button>
                        </div>
                    </form>
                    <style jsx>{`
                .contact-btn {
                    transition: all 0.6s ease-in-out;
                    transition: all 0.3s ease-in;
                }
                .contact-btn:hover {
                    transform: scale(1.05); /* Slightly increase the size */
                }
            `}</style>
                </div>
            </div>
        </>


    )
}



