import React, { useState, useEffect } from "react";
import antDesign from "../assets/antd.png";

export function Skills() {
  const items = [
    { img: "https://img.icons8.com/?size=100&id=20909&format=png&color=000000", name: "HTML5" },
    { img: "https://img.icons8.com/?size=100&id=7gdY5qNXaKC0&format=png&color=000000", name: "CSS3" },
    { img: "https://img.icons8.com/?size=100&id=108784&format=png&color=000000", name: "JavaScript" },
    { img: "https://img.icons8.com/?size=100&id=uJM6fQYqDaZK&format=png&color=000000", name: "TypeScript" },
    { img: "https://img.icons8.com/?size=100&id=3tC9EQumUAuq&format=png&color=000000", name: "GitHub" },
    { img: "https://img.icons8.com/?size=100&id=123603&format=png&color=000000", name: "React JS" },
    { img: "https://img.icons8.com/?size=100&id=123603&format=png&color=000000", name: "React Native" },
    { img: "https://img.icons8.com/?size=100&id=rY6agKizO9eb&format=png&color=000000", name: "Vue Js" },
    { img: "https://img.icons8.com/?size=100&id=jD-fJzVguBmw&format=png&color=000000", name: "Redux" },
    { img: "https://img.icons8.com/?size=100&id=r2OarXWQc7B6&format=png&color=000000", name: "Next.js" },
    { img: "https://img.icons8.com/?size=100&id=rHpveptSuwDz&format=png&color=000000", name: "JWT" },
    { img: "https://img.icons8.com/?size=100&id=g9mmSxx3SwAI&format=png&color=000000", name: "Bootstrap" },
    { img: "https://img.icons8.com/?size=100&id=gFw7X5Tbl3ss&format=png&color=000000", name: "MUI" },
    { img: "https://img.icons8.com/?size=100&id=x7XMNGh2vdqA&format=png&color=000000", name: "Tailwind CSS" },
    { img: antDesign, name: "Ant Design" },
    { img: "https://img.icons8.com/?size=100&id=54087&format=png&color=000000", name: "Node.js" },
    { img: "https://img.icons8.com/?size=100&id=2ZOaTclOqD4q&format=png&color=000000", name: "Express.js" },
    { img: "https://img.icons8.com/?size=100&id=9ESZMOeUioJS&format=png&color=000000", name: "Nest Js" },
    { img: "https://img.icons8.com/?size=100&id=UGYn5TapNioV&format=png&color=000000", name: "PHP" },
    { img: "https://img.icons8.com/?size=100&id=hUvxmdu7Rloj&format=png&color=000000", name: "Laravel" },
    { img: "https://img.icons8.com/?size=100&id=13441&format=png&color=000000", name: "python" },
    { img: "https://img.icons8.com/?size=100&id=114322&format=png&color=000000", name: "AI" },
    { img: "https://img.icons8.com/?size=100&id=IM1zmzuzvwiu&format=png&color=000000", name: "ML" },
    { img: "https://img.icons8.com/?size=100&id=zdI5E8moxhs-&format=png&color=000000", name: "Graphql" },
    { img: "https://img.icons8.com/?size=100&id=bosfpvRzNOG8&format=png&color=000000", name: "MongoDB" },
    { img: "https://img.icons8.com/?size=100&id=11572&format=png&color=000000", name: "MySql" },
    { img: "https://img.icons8.com/?size=100&id=38561&format=png&color=000000", name: "PostgreSQL" },
    { img: "https://img.icons8.com/?size=100&id=62452&format=png&color=000000", name: "Firebase" },
    { img: "https://img.icons8.com/?size=100&id=pHS3eRpynIRQ&format=png&color=000000", name: "Redis" },
    { img: "https://img.icons8.com/?size=100&id=33039&format=png&color=000000", name: "AWS" },
    { img: "https://img.icons8.com/?size=100&id=oROcPah5ues6&format=png&color=000000", name: "Jira" },
    { img: "https://img.icons8.com/?size=100&id=i0Rib28TKAL1&format=png&color=000000", name: "Message Bot" },
  ];

  const [screenConfig, setScreenConfig] = useState(() => getScreenConfig());

  function getScreenConfig() {
    const width = window.innerWidth;
    if (width < 768) {
      return { numRows: 12, circlesPerRow: [3, 2] };
    } else if (width >= 768 && width < 1024) {
      return { numRows: 7, circlesPerRow: [5, 4] };
    } else {
      return { numRows: 3, circlesPerRow: [11, 10] };
    }
  }

  useEffect(() => {
    const handleResize = () => {
      setScreenConfig(getScreenConfig());
    };

    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  const { numRows, circlesPerRow } = screenConfig;
  let itemIndex = 0;

  const newRows = Array.from({ length: numRows }, (_, rowIndex) => {
    const circlesInRow = circlesPerRow[rowIndex % 2];

    return (
      <div className="flex space-x-2 lg:space-x-10" key={rowIndex}>
        {Array.from({ length: circlesInRow }).map((_, circleIndex) => {
          const item = items[itemIndex % items.length];
          itemIndex++;

          return (
            <div key={circleIndex} className="text-center justify-center py-2">
              <div className="skills-img w-24 h-24 p-4 rounded-full overflow-hidden mx-auto border-2 border-[#61cbf5]">
                <img src={item?.img} alt={item?.name} className="w-full h-full object-cover" />
              </div>
              <p className="text-sm mt-2">{item?.name}</p>
            </div>
          );
        })}
      </div>
    );
  });

  return (
    <>
      <p className="text-3xl text-center font-bold py-20 lg:py-20 md:text-4xl lg:text-5xl">Skills</p>
      <div className="flex flex-col justify-center items-center">{newRows}</div>
    </>
  );
}
