import React from 'react'
import { ProjectCard } from './ProjectCard'
import project1 from '../assets/pro 2.png'
import project2 from '../assets/pro 3.png'
import project3 from '../assets/pro 4.png'
import project4 from '../assets/pro 5.png'
export function Projects() {

  const project = [
    {
      name: "Smart Feasi",
      description: "Our client, a seasoned finance professional, aims to empower hotels with comprehensive financial forecasting services, benefiting both established businesses and those planning to expand..",
      imageUrl: project1,
      button: "More Detailed",
    },
    {
      name: "Akademic solution",
      description: "Our client, a seasoned school manager, faced the challenge of manually creating paper tests. This time-consuming and inefficient process hindered their ability to effectively assess student progress. ",
      imageUrl: project2,
      button: "More Detailed ",
    },
    {
      name: "ESQ by Sahel AI",
      description: "ESQ is an Artificial Intelligence (AI) powered tool tailored for African legal teams. It's built by Sahel AI and designed to streamline the contract management process, making it faster, more efficient, and remarkably intuitive.",
      imageUrl: project3,
      button: "More Detailed"
    },
    {
      name: "My African Event",
      description: "My African Events (MAE) connects African communities to local events. The platform lets users create, share, and find events like music festivals, dance groups, and also promotes cultural exchange.",
      imageUrl: project4,
      button: "More Detailed"
    },
    {
      name: "UICM",
      description: "Create tailored CVs for oil rig workers. Track assignments across project disciplines. Showcase your career effectively. Benefit from easy-to-use features, customizable templates, and expert guidance.",
      imageUrl: project2,
      button: "More Detailed",
    }
  ]

  return (
    <div>
    <p className="text-3xl text-center font-bold py-20 lg:py-20 md:text-4xl lg:text-5xl">Projects</p>
      {/* project section */}

      <div className="grid grid-cols-1 px-12 justify-items-center place-content-center md:grid-cols-2 lg:grid-cols-3 hover:text-white ">
        {project.map((projects) => {
          return <ProjectCard name={projects.name} description={projects.description} imageUrl={projects.imageUrl} button={projects.button} link={projects.link} />
        })}
      </div>
    </div >
  )
}

